import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {getOffersDocuments} from "../selectors/offers";
import {getSelectedOfferType} from "../selectors/filters";
import OraOffersDocumentsContainer from "../../core/components/OraOffersDocumentsContainer";

class OraMobileOffersDocumentsContainer extends Component {
    //TODO: think of something better
    getDocumentFilters() {
        return ({
            agreementsList: ["TEMPLATE","AGR_TEMPLATE"],
            pricesList: ["PRICE_LIST", "DEVICE_PRICE_LIST"],
            regulationsList: ["REG", "SHOP_REG"],
            depositList: ["DEP_REQ"]
        });
    }

    render() {
        return <OraOffersDocumentsContainer
            documents={this.props.documents}
            isFut={this.props.isFut}
            filters={this.getDocumentFilters()}
            selectedOfferType={this.props.selectedOfferType}/>;
    }
}

OraMobileOffersDocumentsContainer.propTypes = {
    isFut: PropTypes.string,
    documents: PropTypes.arrayOf(PropTypes.shape({
        documentType: PropTypes.string
    }))
};

const mapStateToProps = state => ({
    selectedOfferType: getSelectedOfferType(state),
    documents: getOffersDocuments(state)
});

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(OraMobileOffersDocumentsContainer);